#calculator-form{
  margin: 0% 25%;
  text-align: center;
  border-radius: 10px;
  background-image: url(https://images.pexels.com/photos/281260/pexels-photo-281260.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260)
}

.flex{
  display: flex;
  justify-content: center;
}


h1#title{
  display: inline-block;
  padding: 2%;
  color: white;
  font-size: 40px;
  text-shadow: 5px 5px 10px black;
}

form{
  margin: 0 auto;
  font-size: 25px;
}

input[type=text]{
  width: 80%;
  padding: 1.5%;
  font-size: 20px;
  border-radius: 5px;
  text-align: right;
  margin: 5px;
  box-shadow: 3px 3px lightskyblue;
}

input[type=text].input-field{
  width: 78%;
}

.submit-button{
  width: 25%;
}

input[type=submit]{
  background-color: rgb(51, 102, 255);
  border: none;
  width: 82.5%;
  color: white;
  font-size: 21px;
  font-weight: bolder;
  padding: 2% 1.5%;
  text-decoration: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 5px;
  margin-left: 1px;
  box-shadow: 2px 2px 5px white;
}

input[type=submit]:hover{
  cursor: pointer;
  opacity: .3;
  color: blue;
  background-color: white;
  text-shadow: 5px 5px 10px black;
  box-shadow: 3px 3px blue;

}

.scrollable-info{
  overflow: scroll; 
  height: 350px;
  display: grid;
  grid-auto-rows: max-content;
  border-top: 2px solid black;
}

.table-headings{
  display: flex;
  justify-content: space-evenly;
  align-self: center;
  border: 1px solid red;
  transition: 3s ease-out;

}
#show-text{
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
}

#show-text:hover{
  cursor: pointer;
  background: white;
  color: black;
  /* color: white; */
  text-shadow: 5px 5px 10px black;
}

#more-info-table{
  display: none;
}
