body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

body{
  background: url(https://finance.advids.co/wp-content/uploads//2017/04/loan-1.gif);
  background-size: cover;
  background-repeat: no-repeat;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
  background: url(https://finance.advids.co/wp-content/uploads//2017/04/loan-1.gif);
  background-size: cover;
  background-repeat: no-repeat;
}



#calculator-form{
  margin: 0% 25%;
  text-align: center;
  border-radius: 10px;
  background-image: url(https://images.pexels.com/photos/281260/pexels-photo-281260.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260)
}

.flex{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}


h1#title{
  display: inline-block;
  padding: 2%;
  color: white;
  font-size: 40px;
  text-shadow: 5px 5px 10px black;
}

form{
  margin: 0 auto;
  font-size: 25px;
}

input[type=text]{
  width: 80%;
  padding: 1.5%;
  font-size: 20px;
  border-radius: 5px;
  text-align: right;
  margin: 5px;
  box-shadow: 3px 3px lightskyblue;
}

input[type=text].input-field{
  width: 78%;
}

.submit-button{
  width: 25%;
}

input[type=submit]{
  background-color: rgb(51, 102, 255);
  border: none;
  width: 82.5%;
  color: white;
  font-size: 21px;
  font-weight: bolder;
  padding: 2% 1.5%;
  text-decoration: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 5px;
  margin-left: 1px;
  box-shadow: 2px 2px 5px white;
}

input[type=submit]:hover{
  cursor: pointer;
  opacity: .3;
  color: blue;
  background-color: white;
  text-shadow: 5px 5px 10px black;
  box-shadow: 3px 3px blue;

}

.scrollable-info{
  overflow: scroll; 
  height: 350px;
  display: grid;
  grid-auto-rows: -webkit-max-content;
  grid-auto-rows: max-content;
  border-top: 2px solid black;
}

.table-headings{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-self: center;
          align-self: center;
  border: 1px solid red;
  transition: 3s ease-out;

}
#show-text{
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
}

#show-text:hover{
  cursor: pointer;
  background: white;
  color: black;
  /* color: white; */
  text-shadow: 5px 5px 10px black;
}

#more-info-table{
  display: none;
}

#summary-title {
  margin-bottom: 0;
  padding: 2%;
  color: white;
  text-shadow: 5px 5px 10px black;
  -webkit-text-decoration: underline black;
          text-decoration: underline black; }

#summary {
  display: grid;
  width: 100%;
  margin: 0 25%;
  padding: 10px;
  text-align: right;
  grid-template-columns: 70% 30%;
  grid-template-rows: 25% 25% 25% 25%; }

div#summary * {
  border-bottom: 2px dashed black;
  color: white;
  font-size: 18px;
  margin: 5px;
  padding: 3px;
  margin-right: 0px; }

#align-left {
  text-align: left; }

.table-headers, .each-month-info {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  padding: 2% 5%; }

.table-headers *, .each-month-info * {
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  padding: 3px; }

.table-headers {
  margin: 0;
  color: white;
  margin-bottom: -3%;
  font-size: 20px;
  text-shadow: 5px 5px 5px black; }
  .table-headers h3 {
    border-bottom: none; }

.each-month-info {
  font-size: 20px; }

